export default "import React from 'react';\r\nimport {store} from 'react-notifications-component';\r\nimport Button from '@material-ui/core/Button';\r\n\r\nimport notification from '../helpers/notification';\r\nimport {getContainer, getMessage, getTitle} from '../helpers/randomize';\r\nimport Box from '@material-ui/core/Box';\r\n\r\nexport default class TypeExample extends React.Component {\r\n  add = type => {\r\n    return store.addNotification(\r\n      Object.assign({}, notification, {\r\n        type,\r\n        title: getTitle(type),\r\n        message: getMessage(type),\r\n        container: getContainer(),\r\n      }),\r\n    );\r\n  };\r\n\r\n  render() {\r\n    return (\r\n      <Box>\r\n        <Box mr={2} my={1} clone>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('success')}>\r\n            Success\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1} clone>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('default')}>\r\n            Default\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1} clone>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('warning')}>\r\n            Warning\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1} clone>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('info')}>\r\n            Info\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1} clone>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('danger')}>\r\n            Danger\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1} clone>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('awesome')}>\r\n            Custom\r\n          </Button>\r\n        </Box>\r\n      </Box>\r\n    );\r\n  }\r\n}\r\n";