export default "import React from 'react';\r\nimport {store} from 'react-notifications-component';\r\nimport Button from '@material-ui/core/Button';\r\n\r\nimport notification from '../helpers/notification';\r\nimport {getMessage, getTitle, getType} from '../helpers/randomize';\r\nimport Box from '@material-ui/core/Box';\r\n\r\nexport default class ContainerExample extends React.Component {\r\n  add = container => {\r\n    const type = getType();\r\n\r\n    return store.addNotification(\r\n      Object.assign({}, notification, {\r\n        title: getTitle(type),\r\n        message: getMessage(type),\r\n        container,\r\n        type,\r\n      }),\r\n    );\r\n  };\r\n\r\n  render() {\r\n    return (\r\n      <Box>\r\n        <Box mr={2} my={1} clone>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('top-left')}>\r\n            Top Left\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1} clone>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('top-right')}>\r\n            Top Right\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1} clone>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('top-center')}>\r\n            Top Center\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1} clone>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('bottom-left')}>\r\n            Bottom Left\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1} clone>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('bottom-right')}>\r\n            Bottom Right\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1} clone>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('bottom-center')}>\r\n            Bottom Center\r\n          </Button>\r\n        </Box>\r\n      </Box>\r\n    );\r\n  }\r\n}\r\n";