export default "import React from 'react';\r\nimport {store} from 'react-notifications-component';\r\nimport Button from '@material-ui/core/Button';\r\nimport {getContainer, getMessage, getType} from '../helpers/randomize';\r\nimport notification from '../helpers/notification';\r\nimport Box from '@material-ui/core/Box';\r\n\r\nconst AnimationEntrance = () => {\r\n  const add = htmlClasses => {\r\n    const type = getType();\r\n    return store.addNotification(\r\n      Object.assign({}, notification, {\r\n        animationIn: htmlClasses,\r\n        container: getContainer(),\r\n        message: getMessage(type),\r\n        type,\r\n      }),\r\n    );\r\n  };\r\n\r\n  return (\r\n    <Box>\r\n      <Box mr={2} my={1} clone>\r\n        <Button\r\n          color='primary'\r\n          variant='contained'\r\n          onClick={() => add(['animated bounceIn'])}>\r\n          Bounce In\r\n        </Button>\r\n      </Box>\r\n      <Box mr={2} my={1} clone>\r\n        <Button\r\n          color='primary'\r\n          variant='contained'\r\n          onClick={() => add(['animated fadeIn'])}>\r\n          Fade In\r\n        </Button>\r\n      </Box>\r\n      <Box mr={2} my={1} clone>\r\n        <Button\r\n          color='primary'\r\n          variant='contained'\r\n          onClick={() => add(['animated flipInX'])}>\r\n          Flip In X\r\n        </Button>\r\n      </Box>\r\n      <Box mr={2} my={1} clone>\r\n        <Button\r\n          color='primary'\r\n          variant='contained'\r\n          onClick={() => add(['animated flipInY'])}>\r\n          Flip In Y\r\n        </Button>\r\n      </Box>\r\n      <Box mr={2} my={1} clone>\r\n        <Button\r\n          color='primary'\r\n          variant='contained'\r\n          onClick={() => add(['animated zoomIn'])}>\r\n          Zoom In\r\n        </Button>\r\n      </Box>\r\n      <Box mr={2} my={1} clone>\r\n        <Button\r\n          color='primary'\r\n          variant='contained'\r\n          onClick={() => add(['animated flash'])}>\r\n          Flash\r\n        </Button>\r\n      </Box>\r\n      <Box mr={2} my={1} clone>\r\n        <Button\r\n          color='primary'\r\n          variant='contained'\r\n          onClick={() => add(['animated jackInTheBox'])}>\r\n          Jack In The Box\r\n        </Button>\r\n      </Box>\r\n    </Box>\r\n  );\r\n};\r\nexport default AnimationEntrance;\r\n";